#main-container {
  margin: auto;
  padding: 0;
  gap: 5px;
  background-color: white;
  color: black;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#emailbox,
#passwordbox,
#confirmpasswordbox {
  display: flex;
  flex-direction: column;
}


.signup {
  margin: auto;
  width: fit-content;
  border-radius: 5px;
  padding: 5px;
  display: flex;
  background-color: blue;
  color: white;
  border: none;
  margin-top: 10px;
}
label {
  font-size: 20px;
  font-weight: 800;
}
input {
  width: 350px;
  height: 25px;
  border: 1px solid black;
}
